@charset "UTF-8";
.form {
  width: 100%;
  height: 40px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .form {
    margin-bottom: 20px;
  }
}

.input {
  border: none;
  outline: none;
  border-radius: 5px;
  min-height: 100%;
  max-height: 100%;
  margin-right: 10px;
  font-size: 1rem;
  padding: 0 5px;
}
.input-color {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-right: 0;
  height: 100%;
  padding: 0 10px;
  background-color: #ffffff;
  cursor: pointer;
}
@media (max-width: 991px) {
  .input-color {
    margin-right: 10px;
  }
}
.input-name, .input-type {
  min-width: 35%;
}
@media (max-width: 991px) {
  .input-name, .input-type {
    min-width: 25%;
  }
}
@media (max-width: 767px) {
  .input-name, .input-type {
    min-width: 25%;
  }
}
@media (max-width: 479px) {
  .input-name, .input-type {
    min-width: 20%;
  }
}

.items-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.item {
  width: calc(100% - 14px);
  background-color: #a4e6f4;
  border: 2px solid #ffffff;
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
  margin-bottom: 5px;
  color: #ffffff;
}
.item:last-child {
  margin-bottom: 0;
}

.selected {
  animation: pulse 1s infinite;
  border: 2px solid #66cccc;
}
@media (max-width: 768px) {
  .selected {
    animation: none;
  }
}

@keyframes pulse {
  0% {
    background-color: #a4e6f4;
  }
  100% {
    background-color: #66cccc;
  }
}
.text {
  width: 40%;
  font-size: 1rem;
  cursor: pointer;
  padding: 5px 0;
  margin-right: 5px;
  overflow-wrap: break-word;
  overflow: hidden;
}
.text-color {
  width: 20%;
}
@media (max-width: 768px) {
  .text {
    width: 30%;
  }
}

.button {
  border-radius: 5px;
  border: none;
  outline: none;
  font-size: 1.25rem;
  padding: 0;
}

.add-button {
  background-color: #0099FF;
  color: #fff;
  height: 100%;
  padding: 0 10px;
  margin-left: auto;
}
.add-button:hover {
  opacity: 0.5;
}
.add-button:active {
  opacity: 1;
}
@media (max-width: 420px) {
  .add-button {
    margin-left: 0 auto;
  }
}

.action-button {
  background-color: inherit;
  color: #393939;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  padding-left: 10px;
}

.delete-button:before {
  content: "";
}

.edit-button:before {
  content: "";
}
.edit-button.active:before {
  content: "";
  color: #006828;
}

button:hover {
  cursor: pointer;
}

html {
  font-size: 16px;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  box-sizing: border-box;
  color: #393939;
  background-color: #b8eefb;
}

.wrapper {
  display: flex;
  justify-content: center;
  min-height: 100%;
  padding-top: 5%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
  width: 1200px;
}

@media (max-width: 1200px) {
  .container {
    width: 970px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 750px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 450px;
  }
}
@media (max-width: 479px) {
  .container {
    max-width: 300px;
  }
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 0 0 30px 0;
  padding: 0;
}

[contenteditable] {
  outline: 0px solid transparent;
}